}

.typewriter_title {
  font-size: 22px;
  font-weight: 500;
  /* font-family: 'Poppins';  */
  /* font-family: 'Geometr415 Lt BT';  */
  font-family: 'Forma DJR Banner'; 
}

.typewriter_title_mobile_landscape {
  font-size: 20px;
  font-weight: 500;
  /* font-family: 'Poppins';  */
  /* font-family: 'Geometr415 Lt BT';  */
  font-family: 'Forma DJR Banner'; 
}

.header_title {
  margin: 0;
  font-size: 42px;
  font-weight: 900;

  font-family: 'f Finish';
}

.header_samll_title {
  font-size: 5px;
  font-weight: 800;
  position: absolute;
  top: 20px;
  left: 237px;
}

.header_title span {
  margin-left: 3px;
  text-decoration: underline;
  text-underline-offset: -19px;
  text-decoration-skip-ink: none;
  text-decoration-color: black;
  text-decoration-thickness: 8px;
}

.section2_row {
  padding: calc((85vh - 467px) / 2);
}

.footer_title {
  opacity: 1;
  font-weight: 800;
  font-size: 27px;
  margin-top: 40px;
  /* font-family: 'Poppins';  */
}
.footer_title.fadeIn {
  opacity: 1;
  transition: opacity 2s;
}

.footer_email {
  opacity: 1;
  font-size: 26px;
  font-weight: 800;
  margin-top: 18vh;
  color: black !important;
  /* font-family: 'Poppins';  */
}

.footer_email.fadeIn {
  opacity: 1;
  transition-delay: 1s;
  transition-property: opacity;
  transition-duration: 2s;
}

.audioIframe {
  display: none;
}

.logo {
  width: 250px;
  height: 40px;
}

.ordinary_title {
  opacity: 1;
  margin: 0;
}

.ordinary_title.fadeIn {
  opacity: 1;
  transition-delay: 2s;
  transition-property: opacity;
  transition-duration: 2s;
}

.sound_icon {
  position: absolute;
  top: 2vh;
  right: 1vw;
  width: 28px;
}

.sound_icon:hover {
  cursor: pointer;
}

.logo_col {
  display: flex;
  justify-content: center;
}

.mobile_typewriter {
  margin-top: 60px;
}

.mobile_section2 {
  padding: 30px 0 0 0 !important;
}

.mobile_end_title {
  margin-top: 30px;
  font-size: 23px;
}

.mobile_email {
  margin-top: 16vh;
  font-size: 23px;
}

.mobile_landscape_logo_row {
  padding: 35px 0 40px 0;
}

.ordinary_title.show_static {
  opacity: 1;
}

.landscape_end_title {
  font-size: 17px;
  font-weight: 900;
  margin-top: 3px;
}

.landscape_email {
  font-size: 18px;
}

